        <p class="my-5">This order will be <b>permanently deleted</b> within 24 hours and <b>cannot be restored</b>, are you sure want to delete this order?</p>
      </div>
      <Button color="red" class="mr-3" @click="handleHardDeleteOrder" :loading="loading.hardDelete">Yes, Delete</Button>
      <Button view="outlined" color="teal" class="bg-white" :disabled="loading.hardDelete" @click="modal.hardDelete = false">Cancel</Button>
    </div>
  </Modal>
    <Modal size="xs" v-model="bulkArchiveModal" title="Archive Order" hide-header hide-footer @close="handleCloseArchiveModal">
      <div class="py-10">
        <img class="mb-10 mx-auto" src="@/assets/icon/attention-orange.svg" alt="Success Icon">
        <div class="mt-8 mb-10">
          <h5 class="mb-4 text-center">Archiving {{ selectedBuybacks.length }} Order<span v-if="selectedBuybacks.length > 1">s</span> </h5>
          <p class="text-center mb-4">
            Are you sure you want to archive {{ selectedBuybacks.length }} order<span v-if="selectedBuybacks.length > 1">s</span> ? <br>
            Once archived orders can be removed from the "Archived" menu
          </p>
        </div>
        <div class="text-center">
          <Button view="outlined" color="teal" class="bg-white" :disabled="bulkArchiveLoading" @click="bulkArchiveModal = false">Cancel</Button>
          <Button color="orange" class="ml-3" :loading="bulkArchiveLoading" @click="archiveOrder">Yes, Archive</Button>
        </div>
      </div>
    </Modal>
    <Modal size="xs" v-model="bulkUnarchiveModal" title="Archive Order" hide-header hide-footer @close="handleCloseUnarchiveModal">
      <div class="py-10">
        <img class="mb-10 mx-auto" src="@/assets/icon/attention-orange.svg" alt="Success Icon">
        <div class="mt-8 mb-10">
          <h5 class="mb-4 text-center">Restoring {{ selectedBuybacks.length }} Order<span v-if="selectedBuybacks.length > 1">s</span> </h5>
          <p class="text-center mb-4">
            Are you sure you want to restore {{ selectedBuybacks.length }} order<span v-if="selectedBuybacks.length > 1">s</span> ? <br>
          </p>
        </div>
        <div class="text-center">
          <Button view="outlined" color="teal" class="bg-white" :disabled="bulkUnarchiveLoading" @click="bulkUnarchiveModal = false">Cancel</Button>
          <Button color="orange" class="ml-3" :loading="bulkUnarchiveLoading" @click="unarchiveOrder">Yes, Restore</Button>
        </div>
      </div>
    </Modal>
</template>

<script>
import { defineComponent, watch, computed, ref, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useStatuses, useCategories, useBuybacks, usePaymentMethods, useBulkStatus, useBulkArchive, useBulkUnarchive, usePrint, useSellingTypes, useExport, useShowNote } from 'composable/buyback'
import { useUserShippingLabel } from 'composable/report'
import { useShippingCarrier } from 'composable/shipping'
import { useCompanyProfile } from 'composable/setting'
import { useBuybackNotes } from  '@/store/pinia/buyback/notes'

import { useUser } from 'composable/user'
import Sources from 'constants/sources'
import Tab from 'bbUi/components/tab/tab.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import SelectCustom from 'components/Select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Card from 'bbUi/components/card/card.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Pagination from 'components/Pagination.vue'
import BulkStatusesForm from 'components/Buyback/BulkStatusesForm.vue'
import MarketPlaceFilter from 'components/Buyback/MarketPlaceFilter.vue'
import Currency from 'components/Currency.vue'
import TextLimit from 'components/TextLimit.vue'
import SelectableChip from 'components/SelectableChip.vue'
import CustomTableManager from 'components/CustomTable/CustomTableManager.vue'
import CustomTableExport from 'components/CustomTable/CustomTableExport.vue'
import BTable from 'components/BTable/BTable.vue'
import FilterDropdown from 'components/FilterDropdown.vue'
import BuybackNotesWarning from '@/components/Features/Buyback/BuybackNotes/BuybackNotesWarning.vue'

import { ElMessage } from 'element-plus'

import { currency } from 'utils/number'
import { upperCaseFirstLetter, kebabCase, titleCase } from 'utils/string'
import { color, sellingTypeColor } from 'utils/status'
import { deepClone } from 'utils/deep-clone'
import debounce from 'utils/debounce'
import { nameCapitalize } from 'utils/element'
import { formatDatabase } from 'utils/date';
import ExpeditedIcon from 'components/ExpeditedPayout/ExpeditedIcon.vue'


export default defineComponent({
  components: {
    Tab,
    Alert,
    Badge,
    GroupContainer,
    Button,
    Input,
    Checkbox,
    Select,
    SelectCustom,
    Pagination,
    Modal,
    Card,
    BulkStatusesForm,
    MarketPlaceFilter,
    Currency,
    TextLimit,
    SelectableChip,
    CustomTableManager,
    CustomTableExport,
    BTable,
    FilterDropdown,
    ExpeditedIcon,
    BuybackNotesWarning
  },
  props: {
    customer: {
      type: Boolean,
      default: false
    }
  },
  async setup(props) {
    const storePiniaNotes = useBuybackNotes()
    const route = useRoute()
    const router = useRouter()
    const isBulkQuote = route.name === 'bulkQuoteTab'

    const { me } = useUser()
    const { state: stateStatuses, stateOption: stateOptionStatuses, tab: tabStatuses, action: actionAllStatuses, actionStatusOptions } = useStatuses(isBulkQuote ? 'bulk-quote' : 'online', props.customer)
    const { tab: categoriesTab } = useCategories(isBulkQuote ? 'bulk-quote' : 'online')
    const { state: statePaymentMethods, loading: loadingPaymentMethods, action: actionPaymentMethods } = usePaymentMethods('online')
    const {
      data,
      isDateShow,
      state: table,
      all,
      actionHardDelete,
      modal,
      updateInTransit,
      actionExportPaypalMass,
      filterList,
      clearFilter,
      clearAllFilter,
      isFiltered,
      filterDateRef,
      filterDateOptions,
      filterOptions,
      filterDateFocus,
      loading,
      openModal,
      isAvailableMasspay,
      masspayPayoutStatus,
      masspayPayoutLabel,
      getFilterOptions
    } = useBuybacks(isBulkQuote ? 'bulk-quote' : 'online', props.customer)
    const { data: bulkStatusData, modal: bulkStatusModal, loading: bulkStatusLoading, actionUpdate: bulkStatusUpdate, unsetData: bulkStatusUnsetData } = useBulkStatus('online')
    const { data: bulkArchiveData, modal: bulkArchiveModal, loading: bulkArchiveLoading, actionBulkArchive, unsetData: bulkArchiveUnsetData } = useBulkArchive('online')
    const { data: bulkUnarchiveData, modal: bulkUnarchiveModal, loading: bulkUnarchiveLoading, actionBulkUnarchive, unsetData: bulkUnarchiveUnsetData } = useBulkUnarchive('online')
    // const { state: stateShippingLabels, action: actionShippingLabels } = useLabels()
    const { shippingCarriers, fetchShippingCarrier } = useShippingCarrier()
    const { loading: loadingPrint, actionBatchCheck: printBatchCheck } = usePrint('online')
    const { state: notes, actionAll: showNotes } = useShowNote('online')
    const { loading: loadingExport, extension: exportExtension, actionCSV: actionExportCSV, actionCSVTremendous: actionExportCSVTremendous, actionCSVDOTS: actionExportCSVDOTS } = useExport(isBulkQuote ? 'bulk-quote' : 'online')
    const { loading: loadingUserShippingLabel, actionBatchPrintAddressLabel, actionBatchPrintShippingKit, actionBatchStatusPrintUpdate } = useUserShippingLabel()
    const { loading: loadingSellingType, state: stateSellingTypes, action: actionSellingType} = useSellingTypes()
    const { companyProfile } = useCompanyProfile()
    const selectedBuybacks = ref([])
    const selected = ref(null)
    const refBuybackTable = ref(null)
    const refBulkStatusForm = ref(null)
    const isClickedIntransitUpdate = ref(false)
    const isClickableTableRow = ref(true)
    const isAppliedFilter = ref(false)
    const isLoadingDropdownFilter = ref(false)
    const reviewed = ref(false)
    const showNotesWarning = ref(false)
    const notAsDescribedOptions = computed(() => {
      const statuses = stateStatuses.value
      const notAsDescribedStatus = statuses.find(status => status.id == 5)
      const subStatuses = !!notAsDescribedStatus ? notAsDescribedStatus.sub : null
      if(subStatuses){
        return subStatuses.map(subStatus => {
          if(subStatus.status_name === 'Revised Offer') return {
            ...subStatus,
            color: 'orange'
          }
          else return {
            ...subStatus,
            color: 'green'
          }
        })
      }
      return []
    })
    const onHoldOptions = computed(() => {
      const statuses = stateStatuses.value
      const onHolds = statuses.find(status => status.id == 10)
      const subStatuses = !!onHolds ? onHolds.sub : null
      if (subStatuses){
        return subStatuses.map(subStatus => {
          return {
            ...subStatus,
            color: 'orange'
          }
        })
      }

      return []
    })
    const leadsOptions = computed(() => {
      const statuses = deepClone(stateStatuses.value)
      const leadsStatus = statuses.find(status => status.id == 1001)
      const subStatuses = !!leadsStatus ? leadsStatus.sub : null
      if(subStatuses){
        return subStatuses.map(subStatus => {
          if(subStatus.status_name === 'Abandoned Buyback') return {
            ...subStatus,
            color: 'orange',
            subStatusId: subStatus.id
          }
          else if(subStatus.status_name === 'Active Leads') return {
            ...subStatus,
            color: 'green',
            subStatusId: subStatus.id
          }
          else if(subStatus.status_name === 'Expired') return {
            ...subStatus,
            color: 'red',
            subStatusId: 'expired'
          }
          // Hide Printer Access "863h1rwgr"
          // else if (subStatus.status_name === 'Shipping Label Request') return {
          //   ...subStatus,
          //   total: null,
          //   color: 'teal',
          //   subStatusId: 'shipping-label'
          // }
          else return {
            ...subStatus,
            color: 'teal',
            subStatusId: subStatus.id
          }
        })
      }
      return []
    })
    const isPendingModal = computed(() => Object.entries(modal).filter(([modalValue]) => modalValue === true).length > 0)
    const filterData = computed(() => {
      const filters = {
        statusId: route.params.statusId,
        expiredStatusId: 0
      }
      const isHasCategoryParam = !!route.params.category
      const isHasSubStatusParam = !!route.params.subStatusId
      const isExpired = route.params.subStatusId ? route.params.subStatusId.toLowerCase() === 'expired' : false
      const isArchived = route.params.category?.toLowerCase?.() === 'archive'
      const isLeads = route.params.statusId === '1001'
      const isLeadsShippingLabel = isLeads && route.params.subStatusId === 'shipping-label'

      if(isHasCategoryParam){
        if(isExpired){
          filters.statusId = 1
          filters.expiredStatusId = 1
        }
        if(isArchived) filters.statusId = 'archive'
      }
      if(isHasSubStatusParam && !isExpired){
        filters.statusId = route.params.subStatusId
      }
      if(isLeadsShippingLabel){
        filters.statusId = 1
      }
      return filters
    })
    const isLeadsShippingLabel = computed(() => route.params.statusId === '1001' && route.params.subStatusId === 'shipping-label')
    const partners = computed(() => {
      if (table.value && table.value.partner && table.value.partner.list && table.value.partner.list.length) {
        return table.value.partner.list
      }
      return []
    })
    const showFilterMarketplace = computed(() => ({
      partner: (table.value && table.value.marketer && table.value.marketer.is_enable) || false,
      marketer: (table.value && table.value.partner && table.value.partner.is_enable) || false
    }))
    const showPartnerTableHead = computed(() => {
      const item = table.value.data && table.value.data.length ? [...table.value.data].shift() : undefined
      const keys = item ? Object.keys(item) : []
      return keys.includes('partner_name')
    })
    const isUnlinkMarketplace = computed(() => {
      return data.marketer && data.marketer.linking_status && (data.marketer.linking_status === 'unlink' || data.marketer.linking_status === 'deleted')
    })
    const isShowChangeStatus = computed(() => {
      if (!data.marketer) return true
      if (!data.marketer.linking_status) return true
      if (!['unlink', 'deleted'].includes(data.marketer.linking_status)) return true

      const selectedReadonly = [...selectedBuybacks.value].filter(item => item.is_readonly)
      if (!selectedReadonly.length) return true

      return false
    })

    const { buyback: buybackSources } = Sources
    const isShowBarcodeScan = computed(() => ['all', '1', '2'].includes(route.params.statusId))
    const isEmptyShippingLabel = computed(() => shippingCarriers.value.length === 0)

    const isSameSelectedStatus = computed(() => {
      const selectedData = selectedBuybacks.value
      return selectedData.every( (val, i, selectedData) => {
        /**
         * Select Status On Hold
         * 9 = General hold
         * 15 = Activation Lock Hold
         */
        if ([9,15].includes(selectedData[0].status_id)) {
          return [9,15].includes(val.status_id)
        }

        /**
         * Select Status Not As Described
         * 12 = Revised Offer
         * 13 = Seller Accepted
         */
        else if ([12,13].includes(selectedData[0].status_id)) {
          return [12,13].includes(val.status_id)
        }

        else {
          return val.status_id === selectedData[0].status_id
        }
      })
    })
    const isBulkStatusLargeModal = computed(() => [7].includes(bulkStatusData.status_id))
    const isInstoreCurrentLocation = computed(() =>  me.value.is_location_mail_in === 0)
    const isPaymentMethodDOTS = computed(() => {
      const methods = [...filterList.value].find(item => item.id === 'payment_method')
      if (methods) {
        const payments = methods.value.split(', ').filter(item =>
          ['bank transfer', 'venmo', 'paypal', 'cash app', 'zelle'].includes(item.toLowerCase())
        )
        const notDOTS = methods.value.split(', ').filter(item =>
          !['bank transfer', 'venmo', 'paypal', 'cash app', 'zelle'].includes(item.toLowerCase())
        )
        if (notDOTS && notDOTS.length) return 0

        return payments && payments.length
      }
      return 0
    })
    const currentBuybackStatusName = computed(() => {
      const isOfferStatusNotActive =  route.params.category?.toLowerCase?.() !== 'active'
      const isOfferStatusExpired = route.params.subStatusId === 'expired'
      if(isOfferStatusNotActive) return 'Archive'
      if(isOfferStatusExpired) return 'Expired'
      if(route.params.statusId === 'all') return 'All'
      const hasSubStatus = route.params.statusId && route.params.subStatusId
      if(hasSubStatus){
        const parentStatus = stateStatuses.value.find(status => status.id == route.params.statusId)
        if(!parentStatus) return ''
        const subStatuses = parentStatus.sub
        if(!subStatuses) return ''
        return subStatuses.find(subStatus => subStatus.id == route.params.subStatusId)?.short_name || ''
      }
      return stateStatuses.value.find(status => status.id == route.params.statusId)?.short_name || ''
    })
    const exportFilename = computed(() => {
      const statusName = currentBuybackStatusName.value
      const [fromDate, toDate] = data.date || []
      const hasDateFilter = fromDate && toDate
      if(!hasDateFilter) return statusName
      const formattedFromDate = fromDate ? formatDatabase(fromDate) : ''
      const formattedToDate = toDate ? formatDatabase(toDate) : ''
      return `${statusName} ${formattedFromDate} to ${formattedToDate}`
    })
    const isMarketplaceFilterApplied = computed(() => !!data.marketer?.id)
    const dropdownFilterOptions = computed(() => {
      if(isBulkQuote){
        return [{
          name: 'Select Date',
          key: 'date',
          count_selected: 0
        }, {
          name: 'Payout Method',
          key: 'payout_method',
          count_selected: (data.payment_method || []).length || 0
        }, {
          name: 'Sales Person',
          key: 'sales_person',
          count_selected: (data.sales_person_name || []).length || 0
        }, {
          name: 'Courier',
          key: 'courier',
          count_selected: (data.shipping_courier.length || []) || 0
        }]
      }
      const filters = [
        {
          name: 'Select Date',
          key: 'date',
          count_selected: 0
        },
        {
          name: 'Item Condition',
          key: 'condition',
          count_selected: (data.item_condition || []).length || 0
        },
        {
          name: 'Referral Source',
          key: 'referral_source',
          count_selected: 0
        },
        {
          name: 'Selling Type',
          key: 'selling_type',
          count_selected: 0
        },
        {
          name: 'Payout Method',
          key: 'payout_method',
          count_selected: (data.payment_method || []).length || 0
        },
        {
          name: 'Source',
          key: 'source',
          count_selected: (data.buyback_source || []).length || 0
        },
        {
          name: 'Courier',
          key: 'courier',
          count_selected: (data.shipping_courier.length || []) || 0
        }
      ]
      // REMOVE MARKETPLACE FEATURE
      // if(showFilterMarketplace.value.partner && partners.value.length) filters.push({
      //   name: 'Partners',
      //   key: 'partner',
      //   count_selected: 0
      // })
      return filters
    })
    const dropdownItemFilterLoading = () => {
      return (
        loading.getFilterOptions ||
        loading.all ||
        loadingSellingType.value ||
        loadingPaymentMethods.value
      )
    }
    const dropdownItemFilterDisableHover = (filter) => {
      return filter.key === 'date'
    }

    const exportInProcess = ref(table.value?.is_csv_download == '1')
    const exportInProcessDots = ref(table.value?.is_csv_download == '1')

    const fetchAll = async () => {
      if(!['buybackOnlineTab', 'bulkQuoteTab'].includes(route.name)) return false
      if(isInstoreCurrentLocation.value) return
      let customerId = null
      isClickedIntransitUpdate.value = false

      const isEmptyStatusId = !route.params.statusId
      const isExpireStatus = route.params.category ? route.params.category.toLowerCase() === 'expired' : false
      const isArchiveStatus = route.params.category ? route.params.category.toLowerCase() === 'archive' : false
      const isLeads = route.params.statusId == '1001'
      const isHasSubStatusParam = !!route.params.subStatusId

      if (isLeads && !isHasSubStatusParam) route.params.subStatusId = '1'  // set default sub status to active leads when status is leads
      if( isEmptyStatusId && !isExpireStatus && !isArchiveStatus ) return router.push({
        name: isBulkQuote ? 'bulkQuoteTab' : 'buybackOnlineTab',
        params: {
          statusId: 'all'
        }
      })

      if(route.name === 'customerDetail' && props.customer) customerId = route.params.customerId
      if(((route.name === 'buybackOnlineTab' || route.name === 'bulkQuoteTab') && !props.customer) || (route.name === 'customerDetail' && props.customer)) {
        await actionAllStatuses({
          ...data,
          status_id: filterData.value.statusId,
          customer_id: customerId,
          is_expired: filterData.value.expiredStatusId,
          selling_type: data.selling_type.value
        })

        // pick first sub status id then use it to update route sub status param
        const currentBuybackStatusId = route.params.statusId
        const currentBuybackStatus = stateStatuses.value.find(status => status.id == currentBuybackStatusId)
        const isEmptySubStatus = !route.params.subStatusId

        if(currentBuybackStatus && isEmptySubStatus){
          const hasSubStatus = currentBuybackStatus.hasOwnProperty('sub')
          if(hasSubStatus){
            const notEmptySubStatus = currentBuybackStatus.sub.length > 0
            if(notEmptySubStatus){
              const selectedSubStatus = currentBuybackStatus.sub[0]
              return router.push({
                name: isBulkQuote ? 'bulkQuoteTab' : 'buybackOnlineTab',
                params: {
                  statusId: route.params.statusId,
                  category: route.params.category,
                  subStatusId: selectedSubStatus.id
                }
              })
            }

          }
        }
        await all(
          filterData.value.statusId,
          customerId,
          filterData.value.expiredStatusId
        )
        await getFilterOptions({
          marketerId: data.marketer?.id,
          statusId: filterData.value.statusId,
          isExpired: filterData.value.expiredStatusId
        })
      }
      exportInProcess.value = table.value?.is_csv_download == '1'
      exportInProcessDots.value = table.value?.is_csv_download == '1'
    }

    watch(() => ({
      route: route.params.statusId,
      limit: data.limit,
      search: data.search,
      payment_method: data.payment_method,
      date: data.date
    }), () => {
      data.page = 1
    }, {
      deep: true
    })

    watch(() => ({
      route: route.params.statusId,
      page: data.page,
      limit: data.limit,
      search: data.search,
      barcode: data.barcode,
      marketplace: data.marketer
      // ...data
    }), debounce(fetchAll, 500), {
        deep: true
    })

    watch(() => ({
      category: route.params.category,
      statusId: route.params.statusId,
      subStatusId: route.params.subStatusId
    }), debounce(async () => {
      selectedBuybacks.value = []
      // refBuybackTable.value.clearSelection()
    }, 500))

    const statusId = computed(() => {
      return route.params.statusId
    })
    const formattedDateFilter = computed(() => {
      const formattedDate = {
        from: null,
        to: null
      }
      const [fromDate, toDate] = data.date || []
      const hasDateFilter = !!fromDate && !!toDate
      if(!hasDateFilter) return formattedDate
      formattedDate.from = formatDatabase(fromDate) || null
      formattedDate.to = formatDatabase(toDate) || null
      return formattedDate
    })
    const isNotAppliedFilters = computed(() => {
      const appliedFilters = filterList.value.filter((filter) => !!filter.value)
      return appliedFilters.length === 0
    })
    const isSamePageBulkStatus = computed(() => {
      return selectedBuybacks.value?.filter(buyback => buyback.page === table.value.current_page)?.length
        ? true
        : false
    })

    const tableSummary = ({columns}) => {
      return columns.map(column => column.property === 'sub_total' ?  `${currency(me.value, table.value.grand_total)}` : '' || column.property === 'qty' ? 'Total' : '')
    }
    const redirectToOrder = (row) => {
      if(!isPendingModal.value) router.push({
        name: isBulkQuote ? 'bulkQuoteDetail' : 'buybackOnlineDetail',
        params: {
          buybackId: isBulkQuote ? row.quote_id : row.offer_id
        }
      })
    }
    const inTransitUpdate = async () => await updateInTransit(async () => {
      isClickedIntransitUpdate.value = true
      await Promise.all([
        all(filterData.value.statusId),
        actionAllStatuses({
          ...data,
          status_id: filterData.value.statusId,
          customer_id: null,
          is_expired: filterData.value.expiredStatusId,
          selling_type: data.selling_type.value
        })
      ])
    })

    const tableOnSelect = (buybacks) => {
      selectedBuybacks.value = buybacks
    }
    const updateStatusBulk = async (callback = undefined) => {
      if(refBulkStatusForm.value.validate && refBulkStatusForm.value.validate() || false) {
        // 3 => FOR COMPLETED
        // 7 => FOR PAYOUT READY/PAYOUT PROCESSING
        if ([3, 7].includes(bulkStatusData.status_id)) {
          bulkStatusLoading.value = true
          storePiniaNotes.warningNotes.ids = bulkStatusData.selected_ids
          !storePiniaNotes.warningNotes.isReviewed && await storePiniaNotes.$getNotesUnread(bulkStatusData.transaction_id.map(i => i.box_id))
          if (storePiniaNotes.warningNotes?.notes?.length) {
            storePiniaNotes.warningNotes.modal = true
            bulkStatusLoading.value = false
            return
          }
        }

        await bulkStatusUpdate(async () => {
          typeof callback === 'function' && callback()
          // refBuybackTable.value.clearSelection()
          selectedBuybacks.value = []

          await Promise.all([
            all(filterData.value.statusId),
            actionAllStatuses({
              ...data,
              status_id: filterData.value.statusId,
              customer_id: null,
              is_expired: filterData.value.expiredStatusId,
              selling_type: data.selling_type.value
            })
          ])
        })
      }
    }
    const getRowKey = (row) => row.id
    const selectableRow = (row) => {
      return true
    }
    const openModalHardDelete = (row) => {
      selected.value = row
      openModal('hardDelete')
    }
    const exportPaypalMass = async (e) => {
      await actionExportPaypalMass(route.params.statusId, route.params.category === 'expired' ? '1' : '0', e)
    }
    const bulkPrintAddressLabel = async () => {
      await actionBatchPrintAddressLabel(selectedBuybacks.value)
    }
    const bulkPrintShippingKit = async() => {
      await actionBatchPrintShippingKit(selectedBuybacks.value)
    }
    const bulkUpdatePrintStatus = async () => {
      await actionBatchStatusPrintUpdate(selectedBuybacks.value, async () => {
        ElMessage({
          message: 'Buyback(s) printed status updated',
          type: 'success'
        })

        // refBuybackTable.value?.clearSelection()
        selectedBuybacks.value = []

        await Promise.all([
          all(filterData.value.statusId),
          actionAllStatuses({
            ...data,
            status_id: filterData.value.statusId,
            customer_id: null,
            is_expired: filterData.value.expiredStatusId,
            selling_type: data.selling_type.value
          })
        ])
      })
    }
    const handleSetSellingType = (sellingType) => {
      data.selling_type.label = sellingType.name
      data.selling_type.value = sellingType.selling_type
    }
    const handleSetPartner = (partner) => {
      data.partner.value = partner.id
      data.partner.label = partner.name
    }
    const handleChangeSubStatus = (subStatusId) => {
      router.push({
        name: 'buybackOnlineTab',
        params: {
          statusId: route.params.statusId,
          category: route.params.category,
          subStatusId: subStatusId
        }
      })
    }

    const handleChangeBulkStatus = () => {
      bulkStatusData.selected_ids = []
      bulkStatusData.selected_box_id = selectedBuybacks.value.map(buyback => buyback.offer_id).join(',')
      bulkStatusData.transaction_id = [...selectedBuybacks.value]
        .filter(buyback => buyback.page === table.value.current_page)
        .map(buyback => {
          bulkStatusData.selected_ids.push(buyback.order_id)
          return ({box_id: buyback.offer_id, transaction_id: ''})
        })
      bulkStatusModal.value = true
      // Set default selected bulk status change id
      const defaultStatusId = selectedBuybacks.value?.length >= 0 ?selectedBuybacks.value[0]?.status_id : 1
      bulkStatusData.status_id = route.params.statusId === 'all' ? defaultStatusId : (!!route.params.subStatusId ? parseInt(route.params.subStatusId) : parseInt(route.params.statusId) )
    }

    const handleArchiveModal = () => {
      bulkArchiveModal.value = true;
    }

    const handleCloseArchiveModal = () => {
      bulkArchiveModal.value = false;
    }

    const handleUnarchiveModal = () => {
      bulkUnarchiveModal.value = true;
    }

    const handleCloseUnarchiveModal = () => {
      bulkUnarchiveModal.value = false;
    }

    const archiveOrder = async () => {
      bulkArchiveData.selected_order_id = JSON.stringify(selectedBuybacks.value.map(buyback => buyback.order_id));
      await actionBulkArchive(async () => {
        ElMessage({
          type: 'success',
          message: 'Success archiving order'
        })
        handleCloseArchiveModal()
        // refBuybackTable.value.clearSelection()
        selectedBuybacks.value = []
        await Promise.all([
          all(filterData.value.statusId),
          actionAllStatuses({
            ...data,
            status_id: filterData.value.statusId,
            customer_id: null,
            is_expired: filterData.value.expiredStatusId,
            selling_type: data.selling_type.value
          })

        ])
        bulkArchiveUnsetData()
      })
    }

    const unarchiveOrder = async () => {
      bulkUnarchiveData.selected_order_id = JSON.stringify(selectedBuybacks.value.map(buyback => buyback.order_id));
      await actionBulkUnarchive(async () => {
        ElMessage({
          type: 'success',
          message: 'Success restoring order'
        })
        handleCloseUnarchiveModal()
        // refBuybackTable.value.clearSelection()
        selectedBuybacks.value = []
        await Promise.all([
          all('archive', null, 0),
          actionAllStatuses({
            ...data,
            status_id: filterData.value.statusId,
            customer_id: null,
            is_expired: filterData.value.expiredStatusId,
            selling_type: data.selling_type.value
          })

        ])
        bulkUnarchiveUnsetData()
      })
    }
    const handleHardDeleteOrder = async () => await actionHardDelete(selected.value, async () => {
      selected.value = null
      modal.hardDelete = false

      ElMessage({
        type: 'success',
        message: 'Order deleted'
      })

      await Promise.all([
        all(filterData.value.statusId),
        actionAllStatuses({
          ...data,
          status_id: filterData.value.statusId,
          customer_id: null,
          is_expired: filterData.value.expiredStatusId,
          selling_type: data.selling_type.value
        })
      ])
    })

    const exportCSV = async (e) => {
			exportExtension.value = e?.extension || e?.ext || e
			if (e.isTremendous)
				await actionExportCSVTremendous()
			else {
				await actionExportCSV({
					status_id: route.params.subStatusId ? route.params.subStatusId : route.params.statusId || route.params.category,
					from: formattedDateFilter.value.from,
					to: formattedDateFilter.value.to,
					is_grouped: e?.grp
				})
				ElMessage({
					type: 'success',
					message: 'Your export data request is processing, it will be delivered to your email when the process is completed.'
				})
				exportInProcess.value = true
				exportExtension.value = 'csv'
			}
    }

    const exportCSVDOTS = async (e) => {
      const payload = {
        payment_method: e.payouts.map(i => i.name),
        delivery_method: e.dots,
        extension: e.extension
      }
      await actionExportCSVDOTS(payload, `${e.extension.toUpperCase()} DOTS`)
      ElMessage({
        type: 'success',
        message: 'Your export data has been sent'
      })
      exportInProcessDots.value = true
    }
    const handleUpdateCustomTable = async () => {
      await fetchAll()
    }
    const handleApplyFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = true
      await fetchAll()
      isLoadingDropdownFilter.value = false
    }
    const handleResetFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = false
      clearAllFilter()
      await fetchAll()
      isLoadingDropdownFilter.value = false
    }
    const handleDropdownItemClick = (filter) => {
      if(!filter) return
      if(filter.key === 'date') filterDateFocus()
    }
    const handleClearFilter = async (filter) => {
      clearFilter(filter)
      await handleApplyFilter()
      if(isNotAppliedFilters.value) isAppliedFilter.value = false
    }

    onMounted(async () => {
      if(isBulkQuote){
        await actionAllStatuses()
        await fetchAll()
        await actionPaymentMethods()
        return
      }
      await actionAllStatuses()
      await fetchAll()
      await Promise.allSettled([
        actionStatusOptions(),
        actionPaymentMethods(),
        fetchShippingCarrier(),
        actionSellingType()
      ])
    })

    if(isInstoreCurrentLocation.value){
      const isCurrentRouteActiveLeads = (route.params.statusId == 1001 && route.params.subStatusId == 1) || (route.params.statusId == 1001 && route.params.subStatusId == '')
      const isCurrentRouteAbandonedBuyback = route.params.statusId == 1001 && route.params.subStatusId == 1000

      let redirectRouteParams = {
        category: 'active',
        statusId: 'all',
        subStatusId: ''
      }
      if(isCurrentRouteActiveLeads){
        redirectRouteParams.statusId = 1001
        redirectRouteParams.subStatusId = 1
      }
      if(isCurrentRouteAbandonedBuyback){
        redirectRouteParams.statusId = 1001
        redirectRouteParams.subStatusId = 1000
      }

      router.push({
        name: 'buybackLocalTab',
        params: redirectRouteParams
      })
    }

    return {
      isBulkQuote,
      me,
      data,
      isDateShow,
      companyProfile,
      bulkStatusData,
      bulkStatusLoading,
      bulkStatusModal,
      bulkPrintAddressLabel,
      bulkPrintShippingKit,
      bulkUpdatePrintStatus,
      buybackSources,
      stateStatuses,
      stateSellingTypes,
      loading,
      loadingPrint,
      loadingUserShippingLabel,
      loadingSellingType,
      table,
      tabStatuses,
      modal,
      categoriesTab,
      selectedBuybacks,
      refBuybackTable,
      refBulkStatusForm,
      isClickedIntransitUpdate,
      isClickableTableRow,
      notAsDescribedOptions,
      leadsOptions,
      onHoldOptions,
      filterList,
      isFiltered,
      isShowBarcodeScan,
      isEmptyShippingLabel,
      isLeadsShippingLabel,
      partners,
      isUnlinkMarketplace,
      isShowChangeStatus,
      showFilterMarketplace,
      showPartnerTableHead,
      statePaymentMethods,
      loadingPaymentMethods,
      actionPaymentMethods,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      tableOnSelect,
      updateStatusBulk,
      getRowKey,
      selectableRow,
      bulkStatusUnsetData,
      color,
      sellingTypeColor,
      upperCaseFirstLetter,
      kebabCase,
      titleCase,
      clearFilter,
      clearAllFilter,
      statusId,
      tableSummary,
      redirectToOrder,
      route,
      inTransitUpdate,
      openModalHardDelete,
      printBatchCheck,
      exportPaypalMass,
      handleSetSellingType,
      handleSetPartner,
      handleChangeSubStatus,
      handleChangeBulkStatus,
      filterOptions,
      tableConfig: {
        isShowSelectionAll: true
      },
      bulkArchiveModal,
      bulkUnarchiveModal,
      handleArchiveModal,
      handleUnarchiveModal,
      handleCloseArchiveModal,
      handleCloseUnarchiveModal,
      archiveOrder,
      unarchiveOrder,
      bulkArchiveLoading,
      bulkUnarchiveLoading,
      isSameSelectedStatus,
      isBulkStatusLargeModal,
      isAvailableMasspay,
      isPaymentMethodDOTS,
      filterData,
      masspayPayoutStatus,
      masspayPayoutLabel,
      exportCSV,
      exportCSVDOTS,
      exportInProcess,
      exportInProcessDots,
      stateOptionStatuses,
      nameCapitalize,
      handleHardDeleteOrder,
      handleUpdateCustomTable,
      handleApplyFilter,
      handleResetFilter,
      handleDropdownItemClick,
      handleClearFilter,
      exportExtension,
      exportFilename,
      dropdownFilterOptions,
      dropdownItemFilterLoading,
      dropdownItemFilterDisableHover,
      isMarketplaceFilterApplied,
      isAppliedFilter,
      isLoadingDropdownFilter,
      isSamePageBulkStatus,
      loadingExport,
      notes,
      showNotes,
      reviewed,
      showNotesWarning
    }
  }
})
</script>

