  .chip{
    &.--selected{
      .substatus-counter{
        &.--color-teal {
          @apply bg-teal-04;
        }
        &.--color-orange{
          @apply bg-orange-04
        }
        &.--color-red{
          @apply bg-red-04;
        }
        &.--color-green{
          @apply bg-green-04;
        }
      }
    }
  }
}
